.order{
    padding: 40px;
    margin: 20px 0;
border: 1px solid lightgray;
background-color: white;
}
.order__id{
   display: flex;
   justify-content: space-between;
}
.order__total{
    font-weight: 500;
    text-align: right;
}
@media only screen and (max-width: 600px) {
    .order__id{
        display: block;
    }
    .order{
        padding: 40px 20px;

    }
  }